import React from 'react';
import SectionContainer from '../../containers/SectionContainer/SectionContainer';
import classes from './About.css';
import self from '../../assets/selfPic.jpg';

const about = (props) =>{
    return ( 
        <SectionContainer title="About Me"> 
            <div className={classes.AboutContainer}>
                <img src={self} alt="Akash Gupta" />
                
                <div className={classes.Content}>
                    <div className={classes.Intro}>
                    <p>Hi, My name is Akash Gupta. I am a final year undergraduate from IIIT Surat, currently pursuing Bachelor's degree in Computer Science. </p>
                    <p> I am an enthusiast Competitive Programmer and Web Developer. I love exploring things &amp; learning new skills. </p>
                    </div>
                    <div className={classes.Skills}>
                        <section >Here are the technologies I love working on: </section>
                        <p> C / C++ </p>    
                        <p> OOPS </p>    
                        <p> React </p>      
                        <p> JavaScript </p>  
                        <p> Firebase </p>
                        <p> MongoDB </p>
                        <p> MySQL </p>
                        <p> HTML &amp; CSS </p>
                        <p> Git </p>

                    </div>
                </div>
            </div>
        </SectionContainer>
    );
}

export default about;


