import React from 'react';
import styled from 'styled-components';
import SectionContainer from '../../containers/SectionContainer/SectionContainer';

const PersonalDetails = styled.section`
    width: 100%;
    height: 60%;
    background-color: white;
`;

 const Content = styled.div`
    height: 80%;
    width: 70%;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: flex-start;
    padding-left: 30%;
    background-color: mediumseagreen;
`;
const Greeting = styled.div`
    font-weight: lighter;
    font-style: italic;
`;
const Name = styled.h1`
    font-weight: bolder;
    font-style: italic;
    font-size: 60px;
`;

const RecruitButton = styled.button`
    font-size: 1em;
    margin: 2% 50%;
    padding: 0.25em 1em;
    border-radius: 5px;
    color: solid green;
    border: 2px solid green;
`;

const mainElement = ()=>{
    return (
        <SectionContainer>
            <PersonalDetails>
                <Content>
                    <Greeting>Hi folks, I am </Greeting>
                    <Name> Akash Gupta </Name>
                    <p> I am an enthusiast Competitive Programmer and Web Developer.</p>
                    <p> I love exploring new things and work on new Projects.</p>
                </Content>

                <a href="mailto:akashgzb98@gmail.com?subject=Recruit Akash Gupta"><RecruitButton> Recruit Me !!</RecruitButton></a>
            </PersonalDetails>
        </SectionContainer>
    );
}

export default mainElement;


