import React from 'react';
import SectionContainer from '../../containers/SectionContainer/SectionContainer';
import styled from 'styled-components';

const ContactContainer = styled.article`
    width: 100%;
    height: 60%;
    display: flex;
    flex-flow: column wrap;
    justify-content: space-between;
    background-color: white;
`;
const ContactTitle = styled.div`
    width:100%;
    font-size: 60px;
    text-align: center;
    font-style: italic;
    background-color: mediumseagreen;
`;

const ContactMessage = styled.div`
    width: 80%;
    display: block;
    text-align: justify;
    padding: 10%;
    background-color: mediumseagreen;
`;

const HelloButton = styled.button`
    font-size: 1em;
    margin: 1em;
    padding: 0.25em 1em;
    margin-left: 60%;
    border-radius: 5px;
    color: solid green;
    border: 2px solid green;
`;

const contact = () =>{
    return ( 
        <SectionContainer title="Contact Me">
            <ContactContainer>
                <ContactTitle> Get In Touch!</ContactTitle>
                <ContactMessage >
                    You are only one step away from me! Get in touch with me to provide your outstanding ideas, remarkable review, 
                    collaboration opportunities, technical discussions, improvement suggestions, social networking or to meet over a coffee. 
                </ContactMessage>
                <a href="mailto:akashgzb98@gmail.com?subject=Hi Akash!"><HelloButton> Say Hi! </HelloButton></a>
            </ContactContainer>
        </SectionContainer>
    );
}

export default contact;


